.description {
  display: flex;
  flex-direction: column;
  margin-top: 30px;

  font-size: 15px;
  margin-left: auto;
  margin-right: auto;
}

.image {
  margin-bottom: 30px;
  border-radius: 50%;

  margin-left: auto;
  margin-right: auto;
}

.textName {
  margin: 0;
  margin-bottom: 10px;

  font-size: 18px;
  font-weight: 500;
  line-height: 1.5;
  text-align: center;
}

.text {
  margin: 0;
  line-height: 1.5;
  font-size: 14px;
  font-weight: 500;
  text-align: center;
  color: grey;
}
.text:last-child {
  margin-bottom: 30px;
}
