.statList {
  display: flex;
  justify-content: space-between;
  border: solid black 1px;
  margin: 0;
  padding: 0;
  background-color: #ede8e8;
}

.statItems {
  display: flex;
  flex-direction: column;
  margin-top: 10px;
  margin-bottom: 10px;
  color: white;
}

.statItems:last-child {
  margin-right: 20px;
}

.statItems:first-child {
  margin-left: 20px;
}

.label {
  font-size: 18px;
  margin-bottom: 5px;
}

.value {
  font-size: 22px;
}
