/* глобальные стили */
li {
  list-style: none;
}

/* стили profile */
.profile {
  display: flex;
  flex-direction: column;
  width: 300px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 20px;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

.profile_items {
  display: flex;
  flex-direction: row;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

/* стили Statistics */
.statistics {
  display: flex;
  flex-direction: column;
  width: 500px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 50px;
  border: solid black 1px;
}

.statisticsTitle {
  margin-left: auto;
  margin-right: auto;
  text-transform: uppercase;
  color: grey;
}
